*{box-sizing: border-box;margin: 0;padding: 0;}
li{list-style: none;}

body{background: #f2f2f2;}

.movies{display: flex;justify-content: space-between;align-items: flex-start;flex-wrap: wrap;width: 80%;margin: 0 auto;}
.movies_movie{display: flex;width: 45%;align-items: flex-start;background: white;margin: 100px 0;padding: 20px;box-shadow: 1px 5px 15px rgba(0,0,0,0.3);min-width: 500px}
.movies_movie img{width: 25%;position: relative;top: -30px;left: 10px;margin-right: 30px;box-shadow: 0 5px 5px rgba(0,0,0,0.2);}

.movie_data{width: 70%;}
.movie_title{color: #666;font-size: 24px;}
.movie_year, .genres{font-size: 14px;}
.movie_year, .genres, .movie_summary{font-weight: 300;color: #aaa;}
.genres{display: flex;margin-bottom: 15px;}
.genres li{display: flex;margin-right: 5px;}